import { create } from "zustand";

export interface TokenTemplate {
  name: string;
  symbol: string;
  description: string;
  website?: string;
  telegram?: string;
  twitter?: string;
  address: string;
  avatarUrl: string; // base64 or blob URL
}

interface TokenTemplatesState {
  templates: TokenTemplate[];
  addTemplate: (template: TokenTemplate) => void;
  removeTemplate: (address: string) => void;
  loadTemplates: () => void;
}

const LOCAL_STORAGE_KEY = "tokenTemplates";

export const useTokenTemplates = create<TokenTemplatesState>((set) => ({
  templates: [],
  addTemplate: (template) =>
    set((state) => {
      const newTemplates = [...state.templates, template];
      localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(newTemplates));
      return { templates: newTemplates };
    }),
  removeTemplate: (address) =>
    set((state) => {
      const newTemplates = state.templates.filter((t) => t.address !== address);
      localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(newTemplates));
      return { templates: newTemplates };
    }),
  loadTemplates: () => {
    const stored = localStorage.getItem(LOCAL_STORAGE_KEY);
    if (stored) {
      set({ templates: JSON.parse(stored) });
    }
  },
}));
