"use client";

import React, { useEffect, useState, ChangeEvent } from "react";
import { useTokenTemplates, TokenTemplate } from "@/store/useTokenTemplates";
import { Card } from "@/components/ui/card";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";

export default function TokenTemplatePage() {
  const { templates, addTemplate, removeTemplate, loadTemplates } = useTokenTemplates();
  const [form, setForm] = useState<Omit<TokenTemplate, "avatarUrl"> & { avatarFile?: File }>({
    name: "",
    symbol: "",
    description: "",
    website: "",
    telegram: "",
    twitter: "",
    address: "",
  });
  const [avatarPreview, setAvatarPreview] = useState<string>("");

  useEffect(() => {
    loadTemplates();
  }, [loadTemplates]);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target;
    setForm((prev) => ({ ...prev, [name]: value }));
  };

  const handleAvatarChange = (e: ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      setForm((prev) => ({ ...prev, avatarFile: file }));
      const reader = new FileReader();
      reader.onloadend = () => {
        setAvatarPreview(reader.result as string);
      };
      reader.readAsDataURL(file);
    }
  };

  const handleSave = () => {
    if (!form.name || !form.symbol || !form.address) {
      alert("Please fill in required fields: Name, Symbol, Address");
      return;
    }
    const newTemplate: TokenTemplate = {
      name: form.name,
      symbol: form.symbol,
      description: form.description,
      website: form.website,
      telegram: form.telegram,
      twitter: form.twitter,
      address: form.address,
      avatarUrl: avatarPreview,
    };
    addTemplate(newTemplate);
    setForm({
      name: "",
      symbol: "",
      description: "",
      website: "",
      telegram: "",
      twitter: "",
      address: "",
    });
    setAvatarPreview("");
  };

  return (
    <div className="space-y-6">
      {/* Top sticky panel */}
      <Card className="sticky top-0 bg-gray-900 border border-blue-500 rounded-md p-6 z-10">
        <h2 className="text-lg font-semibold mb-4">Create new token template</h2>
        <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
          <Input
            name="name"
            value={form.name}
            onChange={handleInputChange}
            placeholder="Name"
            className="border border-blue-400 rounded-md p-2"
            required
          />
          <Input
            name="symbol"
            value={form.symbol}
            onChange={handleInputChange}
            placeholder="Symbol"
            className="border border-blue-400 rounded-md p-2"
            required
          />
          <Textarea
            name="description"
            value={form.description}
            onChange={handleInputChange}
            placeholder="Description"
            className="border border-blue-400 rounded-md p-2 col-span-1 md:col-span-2"
          />
          <Input
            name="website"
            value={form.website}
            onChange={handleInputChange}
            placeholder="Website"
            className="border border-blue-400 rounded-md p-2"
          />
          <Input
            name="telegram"
            value={form.telegram}
            onChange={handleInputChange}
            placeholder="Telegram"
            className="border border-blue-400 rounded-md p-2"
          />
          <Input
            name="twitter"
            value={form.twitter}
            onChange={handleInputChange}
            placeholder="Twitter"
            className="border border-blue-400 rounded-md p-2"
          />
          <Input
            name="address"
            value={form.address}
            onChange={handleInputChange}
            placeholder="Address"
            className="border border-blue-400 rounded-md p-2"
            required
          />
          <div className="flex flex-col items-start">
            <label className="mb-1 font-medium">Avatar</label>
            <input type="file" accept="image/*" onChange={handleAvatarChange} />
            {avatarPreview && (
              <img src={avatarPreview} alt="Avatar Preview" className="mt-2 h-20 w-20 rounded-md object-cover border border-blue-400" />
            )}
          </div>
        </div>
        <div className="mt-4">
          <Button onClick={handleSave} className="bg-blue-600 hover:bg-blue-700">
            Save Template
          </Button>
        </div>
      </Card>

      {/* Bottom panel */}
      <Card className="border border-blue-500 rounded-md p-4 max-h-[400px] overflow-auto">
        <h2 className="text-lg font-semibold mb-4">Prepared token templates</h2>
        {templates.length === 0 ? (
          <p className="text-gray-400 italic">No templates saved yet.</p>
        ) : (
          <ul className="space-y-3">
            {templates.map((template) => (
              <li
                key={template.address}
                className="flex items-center gap-4 p-3 border border-transparent rounded-md hover:border-blue-400 cursor-pointer transition"
              >
                {template.avatarUrl ? (
                  <img src={template.avatarUrl} alt={template.name} className="h-12 w-12 rounded-md object-cover" />
                ) : (
                  <div className="h-12 w-12 rounded-md bg-gray-700 flex items-center justify-center text-gray-400">No Avatar</div>
                )}
                <div className="flex-1">
                  <p className="font-semibold">{template.name}</p>
                  <p className="text-sm text-gray-300">{template.symbol}</p>
                  <p className="text-xs text-gray-400 truncate max-w-md">{template.description}</p>
                  <p className="text-xs text-gray-400 truncate max-w-md">{template.address}</p>
                </div>
                <button
                  onClick={() => removeTemplate(template.address)}
                  className="text-red-500 hover:text-red-700 font-semibold"
                  aria-label={`Delete template ${template.name}`}
                >
                  Delete
                </button>
              </li>
            ))}
          </ul>
        )}
      </Card>
    </div>
  );
}
